@import url('https://fonts.googleapis.com/css2?family=Cairo:wght@900&display=swap');
.projects{
    padding:10px;
    font-family: 'Cairo', sans-serif;
    font-size:1.2rem;
}
/* projects */

@media screen and (max-width:991px){
    .pr{
        opacity:1;
    }
}
.project-card{
    background-color:#0d63fd;
    border: 5px #0d63fd solid;
    border-radius: 10px;
    box-shadow:0 0 20px #0d63fd;
}

.pr-img{
    border-radius:20px;
    padding:5px;
}

.pr-head{
    text-align:center;
}

.txt{
    color:#66fcf1;
}

.a{
    color:violet;
}

.a:hover{
    color:red;
}